import React from "react";

const date = new Date();
const year = date.getFullYear();



export class Footer extends React.Component{
    handleClick(){
        window.open("https://jaidataylor.tech");
    }
    handleNewClick(){
        window.open("https://github.com/jtaylor1204/Keeper-App");
    }
    render() {
        return (
            <footer>
                <p>This app was coded by 
                <a className = "cursor" onClick = {this.handleClick}> Jaida Taylor </a> 
                in {year}, and is 
                <a className="cursor" onClick= {this.handleNewClick}> open-sourced. </a>
                </p>
            </footer>
        )
    
    }
}